    position: relative;
  }

  .modal_box:placeholder-shown {
    border: 1px solid #d7d7d7;
  }

  .modal_box:placeholder-shown ~ .modal_box__valid, .modal_box:placeholder-shown ~ .modal_box__invalid {
    visibility: hidden;
