  .modal_box:not(:placeholder-shown):required:valid ~ .modal_box__valid {
    visibility: visible;
  }

  .modal_box__valid {
    height: 20px;
    width: 20px;
    fill: #3cbc81;
    visibility: hidden;
    position: absolute;
    transform: translate(-25px, 8px);
  }

  .modal_box__invalid {
    height: 20px;
    width: 20px;
    fill: #e74a3b;
    visibility: hidden;
    position: absolute;
    transform: translate(-25px, 8px);
  }

  .modal_box:not(:placeholder-shown):invalid {
    border: 1px solid #e74a3b;
  }

  .modal_box:not(:placeholder-shown):invalid ~ .modal_box__invalid {
    visibility: visible;
  }

